textarea ~ .counter {
    color:$gray-600;
    width:100%;
    text-align:right;
    display:inline-block;
}



// ------------------------------------------------------------------------------ Restyling Bootstrap Defaults
.form-group {
    .character-counter {
        color:$gray-600;
        text-align:right;
        display:block;
    }
}
.form-control {
    color:$gray-900;
    &:disabled, &[readonly] {
        background-color: #eee;
        color:$gray-700;
    }
}

label, .like-label {
    display:block;
    font-weight:bold;
    line-height:1.1;
    margin-bottom:.3rem;
    .optional {
        font-weight:normal;
        color:$gray-600;
        font-style:italic;
    }
}


// ------------------------------------------------------------------------------ Restyling Select2 Angular

.select2-container {
    width: 100%!important;
    .selection {
        .select2-selection {
            min-height: $input-height;
            border: $input-border-width solid $input-border-color;
            .select2-selection__rendered {
                color:$input-color;
                line-height: $input-line-height;
                padding: $input-padding-y $input-padding-x;
                .select2-selection__clear { // clear selection "button"
                    width: calc( #{$input-height-inner} - ( #{$input-padding-y} * 2 ) );
                    height: calc( #{$input-height-inner} - ( #{$input-padding-y} * 2 ) );
                    background:$gray-100;
                    position:absolute;
                    right:1.5rem;
                    border-radius:1rem;
                    text-align:center;
                    color:$gray-900;
                    &:hover {
                        color:$gray-900;
                        background-color:$gray-200;
                    }
                }
            }
            &.select2-selection--multiple { // multiple selects
                display:flex;
                flex-wrap: wrap;
                .select2-selection__rendered {
                    padding: .25rem $input-padding-x 0rem $input-padding-x;
                    height:100%;
                    .select2-selection__choice {
                        margin-top: 0rem;
                        margin-bottom: .25rem;
                        background:$blue-100;
                        border-color:$blue-300;
                        padding: 1px 5px 1px 2px;
                        .select2-selection__choice__remove {
                            color:$blue-700;
                            padding:0 .25rem;
                            margin-right:0px;
                            &:hover {
                                color:$blue-900;
                            }
                        }
                    }
                    .select2-search .select2-search__field {
                        margin-top:2px;
                    }
                    .select2-selection__clear {
                        right:.5rem;
                        cursor: pointer;
                        float: unset;
                        font-weight: bold;
                        margin-top: 2px;
                        margin-right: unset;
                        padding: unset;
                    }
                }
            }
        }
        .select2-selection__arrow { // undoing the default select2 caret
            height:$input-height-inner;
            width:1rem;
            b {
                border-color:transparent;
                border-style:unset;
                border-width:unset;
                height: unset;
                width: unset;
                margin-left: unset;
                margin-top: unset;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translate(0, -50%);
                &:before {
                    content:'\f0d7';
                    font-family: 'Font Awesome 5 Pro';
                    display:inline-block;
                    color:$gray-600;
                }
            }
        }
    }
    &.select2-container--focus .select2-selection {
        border-color: $input-focus-border-color;
        box-shadow: $input-focus-box-shadow;
    }
}
.select2-search--dropdown .select2-search__field {
    width: 98%;
}


// ------- Validation 
select.is-invalid, select:invalid {
   & ~ span.select2-container {
        .selection {
            .select2-selection {
                border-color: $form-feedback-invalid-color;
            }
        }
        &.select2-container--focus .select2-selection {
            box-shadow: 0 0 0 $input-btn-focus-width rgba($form-feedback-invalid-color, .25);
        }
    }
}

// -------------------------------- Input Groups
.input-group {
    ng-select2 {
        position: relative;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        width: 1%;
        margin-bottom: 0;
        &:not(:first-of-type) {
            .select2-container--default .select2-selection--single {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
                margin-left:-1px;
            }  
        }
        &:not(:last-of-type) {
            .select2-container--default .select2-selection--single {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }
        }
    }
    .input-group-text {
        &:not(:first-child) {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            margin-left:-1px;  
        }
        &:not(:last-child) {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
}

// ------------------------------------------------------------------------------ Check and Radio

$checkbox-size: 1rem;

.form-check {
    input {
        position: absolute;
        margin-top: .3rem;
        margin-left: -1.25rem;
        z-index: -1;
        opacity: 0;
        &:hover {
            cursor:pointer;
        }
        ~ label {
            &:hover {
                cursor:pointer;
            }
        }
        // replaces default radios and checks with fontawesome icons
        ~ label:before {
            font-family: 'Font Awesome 5 Pro';
            font-weight: 400;
            display: inline-block;
            margin-left: -1.2rem;
            margin-right:.3rem;
            color:$gray-900;
            width: $checkbox-size;
            height: 1.1rem;
            font-size: $checkbox-size;
            line-height: 1.1rem;
            border-radius:.2rem;
            text-align:center;
            transition: box-shadow 0.15s ease-in-out;
        }
        &:checked ~ label:before {
            font-weight:900;
            color:$primary;
        }
        &:focus ~ label:before {
            box-shadow: 0 0 0 .1rem rgba($primary, .25), inset 0 0 0 .2rem rgba($primary, .25);
        }
        &:disabled ~ label:before {
            color:$gray-600;
        }
        &[type="radio"] {
            ~ label:before {
                content: '\f111';
                border-radius: 1rem;
                width: 1.1rem;
            }
            &:checked ~ label:before {
                content: '\f192';
            }
        }
        &[type="checkbox"] {
            ~ label:before {
                content: '\f0c8';
            }
            &:checked ~ label:before {
                content: '\f14a';
            }
        }
    }
}

.form-check-inline, .form-inline .form-check {
    input {
        position: static;
        margin-top: 0;
        margin-right: .3125rem;
        margin-left: 0;
    }
}

// ------------------------------------------------------------------------------ Grant Number Fields

$alphaWidth: .9rem; // width of alpha characters
$numWidth: .55rem; // width of numeric characters

.grant-number-container {
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    padding-top:1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding:1rem;
    margin-bottom:1rem;
    @media (min-width: 768px) { // at size md and up
        padding-top:0rem;
        border-color:transparent;
        padding:0rem;
        margin-bottom: 0rem;
    }
    .form-group {
        margin-right:10px; // same as form-row gutters 
        label {
            font-weight:normal;
        }
        input {
            text-align:center;
        }
    }
    #grant-number-legend {
        max-width:none;
        display:block;
        width:100%;
        margin-bottom:.0rem;
        margin-top:-1.5rem;
        margin-left:-1rem;
        text-shadow: 1px 1px 0px #fff, 1px -1px 0px #fff, -1px -1px 0px #fff, -1px 1px 0px #fff;
        margin-right:0rem;
    }
    @media (min-width: 768px) { // at size md and up
        #grant-number-legend {
            width:auto;
            max-width:4rem;
            display:inline-block;
            margin-top:.15rem;
            margin-right:.5rem;
        }
    }
    #grantNumberType {
        width: calc(1.5rem + #{$alphaWidth});
    }
    #grantNumberIC, #grantNumberYear {
        width: calc(1.5rem + (#{$alphaWidth} * 2));
    }
    #grantNumberMech {
        width: calc(1.5rem + (#{$alphaWidth} * 3));
    }
    #grantNumberSuffix {
        width: calc(1.5rem + (#{$alphaWidth} * 4));
    }
    #grantNumberSerial {
        width: calc(1.5rem + (#{$numWidth} * 6));
    }
    .btn-clear-fields {
        mask: url("../images/times-tag.svg");
        background:$gray-600;
        mask-size:contain;
        mask-repeat: no-repeat;
        mask-position: center;
        width:2rem;
        height:calc(1.5em + 0.75rem + 2px);
        display:inline-block;
        margin-bottom: 0.1rem;
        &:hover, &:focus {
            background:$gray-800;
        }
    }
}
