.btn:disabled {
    cursor:not-allowed;
}

.btn-outline-primary:not([disabled]):hover {
    background-color: rgba($primary, .15);
    color:darken($primary, 15%);
}
.btn-outline-secondary:not([disabled]):hover {
    background-color: rgba($secondary, .15);
    color:darken($secondary, 15%);
}
.btn-outline-success:not([disabled]):hover {
    background-color: rgba($success, .15);
    color:darken($success, 15%);
}
.btn-outline-danger:not([disabled]):hover {
    background-color: rgba($danger, .15);
    color:darken($danger, 15%);
}
.btn-outline-warning:not([disabled]):hover {
    background-color: rgba($warning, .15);
    color:lighten($warning, 15%);
}
.btn-outline-info:not([disabled]):hover {
    background-color: rgba($info, .15);
    color:darken($info, 15%);
}
.btn-outline-light:not([disabled]):hover {
    background-color: rgba($light, .15);
    color:lighten($light, 15%);
}
.btn-outline-dark:not([disabled]):hover {
    background-color: rgba($dark, .15);
    color:darken($dark, 15%);
}

.btn-link {
    &:hover, &:focus {
        background-color: rgba($primary, .1);
        transition: all .2s, color .2s!important;
    }
}

// back button instead of breadcrumbs
.btn-back {
    margin-left:-.5rem;
    margin-top:-.3rem;
    margin-bottom:.4rem;
}

// help buttons
.btn-help {
	line-height:0;
    padding:0rem;
}

.btn-slim {
    padding:.2rem .3rem;
}

.btn-delete {
    &.btn-link {
        color:$red-700;
        &:hover, &:focus {
            background-color: rgba($red-700, .1);
            transition: all .2s, color .2s!important;
        }
    }
}