#skipNav {
  &:focus {
    text-align:center;
    padding:5px;
    width:100%;
  }
}

#i2eHeader {
  padding: .8rem 1rem;
  background: #013b51 url("../images/I2E_Header-BG.png") no-repeat center;
  background-size:cover;
  border-bottom: $teal 5px solid;
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
  .navbar-brand {
    background: url("../images/NIH_LogoIcon_White.svg") no-repeat;
    height: 2.5rem;
    width: 4rem;
    margin-right:0rem;
  }
  @include media-breakpoint-up(md) { 
    .navbar-brand {
      background: url("../images/I2E_Logo_White.svg") no-repeat;
      width: 20rem;
    }  
  }
  .navbar-toggler {
    padding: 0.35rem 0.55rem;
    border-color:rgba(255,255,255,0.7);
    background: $dark;
    &:hover, &:focus {
      background: $primary;
    }
    .navbar-toggler-icon {
      background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(255,255,255,1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    }
  }
  .appName {
    color:#fff;
    font-family: "Montserrat",sans-serif;
    font-weight: 700;
    text-shadow: 2px 2px 2px $dark;
    font-size:1.1rem;
    text-align: center;
    line-height: 1.1;
    word-break: break-word;
  }
  @include media-breakpoint-up(md) {
    .appName {
      word-break: normal;
      font-size:1.3rem;
      line-height:1.3rem;
    }
  }
  .navbar-collapse {
    flex-grow:0;
    text-align:center;
    padding-top: 0rem;
    border-top:none;
    margin-top: 0rem;
    &.show {
      padding-top: .3rem;
      border-top:1px solid rgba(255,255,255,.5);
      margin-top: .8rem;
      transition: all 200ms;
    }
    .nav-item {
      .nav-link {
        display:inline-block;
        color: rgba(255,255,255,1);
        font-size:$font-size-sm;
        &:hover {
          box-shadow: inset 0 -0.2rem $teal;
          transition: box-shadow .2s, color .2s;
        }
      }
      .navbar-text {
        color: rgba(255,255,255,1);
        font-size:$font-size-sm;
        padding:.5rem;
      }
      .dropdown-menu {
        .dropdown-item {
          padding: 0.25rem 1rem;
          &:hover {
            background-color: $light;
            transition: background-color 0.15s ease-in-out,
          }
        }
      }
      &.change-user {
        .dropdown-menu {
          padding:.5rem 1rem 1rem 1rem;
          min-width:18rem;
          .form-group {
            margin-bottom:0rem;
          }
        }
        @include media-breakpoint-up(md) {
          a {
            font-size: 1.2rem;
            padding: .2rem .5rem;
          }
        }
      }
      .dropdown-toggle::after { // remove caret from dropdowns
        display: inline-block;
        margin-left: 0rem;
        vertical-align: 0rem;
        content: none;
        border-top: none;
        border-right: none;
        border-bottom: 0;
        border-left: none;
      }
    }
  }
  @include media-breakpoint-up(md) {
    .navbar-collapse.show, .navbar-collapse {
      padding-top: 0rem;
      border-top:none;
      margin-top: 0rem;
    }
  }
}


#secondNav {
  padding:0rem;
  border-bottom:1px solid $gray-200;
  ul.nav {
    width:100%;
    margin-right:none;
    text-align:center;
    position: relative;
    li.nav-item {
      font-size:$font-size-sm;
      a.nav-link {
        padding: 0.6rem 0.9rem;
        color:$gray-900;
        border-radius: 0rem;
        &:hover, &:focus {
          background: $gray-100;
        }
        &.active {
          background: $secondary;
          color:#fff;
          &:hover {
            background: $gray-700;
          }
        }
      }
    }
  }
  #i2eHome {
    a.nav-link {
      padding: 0.4rem 0.8rem;
      font-size: 1.1rem;
      color:$gray-700;
    }
  }
  #impac2 {
    position:absolute;
    right:1px;
  }
}

// scrollable tabs
#secondNav {
  .scrtabs-tab-container, .scrtabs-tabs-fixed-container, ul.nav, li.nav-item, a.nav-link {
    height: 2.4rem;
  }
  .scrtabs-tab-container {
    margin-left: -15px;
    margin-right: -15px;
    display:flex;
    .scrtabs-tab-scroll-arrow {
      border:none;
      height:2.4rem;
      width:2rem;
      font-size:$font-size-base;
      padding: 0.55rem 0.5rem;
      color:$primary;
      transition: box-shadow .6s ease, color .2s ease;
      &:hover, &:focus {
        background-color:$light;
        color:$blue-900;
      }
      &.scrtabs-tab-scroll-arrow-right {
        border-left:1px solid $gray-200;
        text-align: right;
        box-shadow: 7px 0px 15px 15px rgba(0,0,0,0.2);
        clip-path: polygon(calc(100% - 70px) 0px, 100% 0, 100% 100%, calc(100% - 70px) 100%);
        margin-left:auto;
        //ie only
        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          clip-path:none!important;
          box-shadow: -5px 0px 10px -3px rgba(0,0,0,0.2);
        }
      }
      &.scrtabs-tab-scroll-arrow-left {
        border-right:1px solid $gray-200;
        text-align: left;
        box-shadow: -7px 0px 15px 15px rgba(0,0,0,0.2);
        clip-path: polygon(0 0, calc(100% + 70px) 0, calc(100% + 70px) 100%, 0 100%);
        margin-right:auto;
        //ie only
        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          clip-path:none!important;
          box-shadow: 5px 0px 10px -3px rgba(0,0,0,0.2);
        }
      }
      &.scrtabs-disable {
        color:$gray-300;
        background-color:#fff;
        box-shadow: none;
      }
    }
  }
}

