
$table-border: 1px solid $gray-400;

.table-striped-reverse {
    tbody {
        tr {
            &:nth-of-type(2n+1) {
                background-color:transparent;
            }
            &:nth-of-type(2n) {
                background-color: rgba(0, 0, 0, 0.05);
            }
        }
    }

}

// dataTable styling
table.dataTable {
  border: $table-border;
  border-left-width:0px;
  border-bottom-width:0px;
  table-layout:auto!important;
  border-collapse:collapse!important;
  tfoot th {
    border:$table-border;
    border-top-width:1px;
    border-right-width:0px;
    border-bottom-width:1px;
  }
  thead th {
    border:$table-border;
    border-top-width:0px;
    border-right-width:0px;
    border-bottom-width:2px;
    line-height:1;
    padding: 8px 16px 8px 10px;
    &.no-sort {
      background: none;
      pointer-events: none;
      &:after {
        content:none!important;
      }
    }
    &:not(.select-checkbox):before {
      content:none!important;
    }
  }
  tbody {
    tr {
      &.odd {
        background-color:$light;
        & + .child {
          background-color:$light;
          &:hover {
            background-color:$light!important;
          }
        }
      }
      &.parent {
        td {
          border-bottom-style:dashed;
        }
      }
      td {
        border:$table-border;
        border-top-width:0px;
        border-right-width:0px;
        vertical-align: top;
        line-height:1.2;
        &.child {
          padding: .3rem .75rem .4rem .75rem;
          ul.dtr-details {
            width:100%;
            li {
              width:100%;
              padding:.4rem 0;
              border-bottom: 1px solid rgba(0,0,0,.2);
              &:last-child {
                border-bottom:none;
              }
              span.dtr-title {
                margin-right:.3rem;
                min-width:unset;
              }
            }
          }
        }
      }
    }
  }
}

// responsive table styles

table.dataTable.dtr-column {
  td, th {
    &.control {
      border-left-width:0px;
    }
  }
  tbody {
    tr > td.control {
      &:before {
        font-family: "Font Awesome 5 Pro";
        font-weight: 900;
        content: '\f055';
        color: $blue-900;
        background-color:#fff;
        border:none;
        box-shadow:none;
        top: 0.8rem;
        left: 0rem;
        margin-top:0rem;
        margin-left:0rem;
        height: unset;
        width: unset;
        font-size: 1.2rem;
      }
      &:hover:before {
        color: $primary;
        box-shadow: $box-shadow-sm;
      }
    }
    tr.parent > td.control {
      &:before {
        content: '\f056';
        color: $gray-500;
        background-color:#fff;
      }
      &:hover:before {
        color: $gray-600;
      }
    }
  }
}

// table info elements
div.dataTables_wrapper {
  .table-controls {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    flex-wrap: wrap;
    > div {
      display:flex;
      align-items:flex-start;
    }
    .dataTables_filter {
      label {
        margin-bottom:0rem;
      }
    }
    div.dataTables_info { // "showing # of # entries" text
      padding-top:0rem;
      font-size:$font-size-sm;
    }
    div.dataTables_length { // "show # entries" text
      font-size: $font-size-sm;
      label {
        font-size: $font-size-sm;
        margin-bottom:0rem;
        .form-control {
          padding: .25rem 0.3rem;
        }
      }
    }
    // pagination
    ul.pagination {
      margin:0rem!important;
      .page-item {
        .page-link {
          font-size:$font-size-sm;
          padding: 0.35rem 0.6rem;
          color: $primary;
          &:hover {
            background-color:$light;
            color: $blue-900;
          }
        }
        &.disabled {
          .page-link {
            background-color:$light;
            color:$gray-400;
          }
        }
        &.active {
          .page-link {
            color:#fff;
            background-color:$secondary;
            border-color:$secondary;
            &:hover {
              background-color: $gray-700;
              border-color: $gray-700;
            }
          }
        }
      }
    }
  }
}
// selectable tables
table.dataTable tbody tr, table.dataTable thead tr {
  .select-checkbox {
    text-align: center;
    &:hover {
      cursor:pointer;
    }
    &:before {
      font-family: "Font Awesome 5 Pro";
      font-weight: 400;
      content: '\f0c8';
      border:none;
      color:$gray-500;
      position:unset;
      top:unset;
      left:unset;
      width: $checkbox-size;
      height:$checkbox-size;
      margin-top: 0.01rem;
      margin-left: 0.01rem;
      font-size: $checkbox-size;
      line-height: $checkbox-size;
      display:inline-block;
      border-radius:.2rem;
    }
    &.selected:before {
      color: $blue-900;
      font-family: "Font Awesome 5 Pro";
      font-weight: 900;
      content: '\f14a';
    }
    &:focus:before {
      box-shadow: 0 0 0 .1rem rgba($primary, .25), inset 0 0 0 .2rem rgba($primary, .25);
    }
  }
  &.selected {
    color:#333;
    &.even {
      background-color: $blue-100;
      + tr.child, + tr.child:hover {
        background-color: $blue-100!important;
      }
    }
    &.odd {
      background-color: lighten($primary, 50%)!important;
      + tr.child, + tr.child:hover {
        background-color: lighten($primary, 50%)!important;
      }
    }
    a {
      color:$primary;
      &.btn-primary, &.btn-danger, &.btn-secondary, &.btn-success, &.btn-dark {
        color:white;
      }
      &.btn-outline-primary {
        color:$primary;
      } 
      &.btn-outline-secondary {
        color:$secondary;
      }
    }
    .select-checkbox {
      &:before {
        font-family: "Font Awesome 5 Pro";
        font-weight: 900;
        content: '\f14a';
        color: $blue-900;
      }
      &:after {
        content:none;
      }
      &:focus:before {
        box-shadow: 0 0 0 .1rem rgba($primary, .25), inset 0 0 0 .2rem rgba($primary, .25);
      }
    }
  }
}