// ============================================= HTML Components ============================================= //

// structural
body, body > app-root {
    display:flex;
    min-height:100vh;
    flex-direction:column;
    font-family: $noto-sans;
    line-height: 1.4;
}

main {
    margin:.9rem 0rem 2rem 0rem;
}

#site-content {
    flex: 1;
}

a {
    &:focus, &:hover {
        text-decoration:none!important;
    }
}

hr {
    width:100%;
    display:block;
    border-top-color:$gray-300;
}

blockquote {
    padding:0rem 1rem;
    border-left:.5rem solid $light;
    > cite {
        display:block;
        text-align:right;
    }
}

cite {
    font-size:90%;
}

pre {
    margin-bottom: 0rem;
}

code {
    font-size: $font-size-sm;
    color: $teal-700;
    word-break: break-word;
    background-color: $teal-100;
}

.external-link:after {
    content: '\f35d';
    font-family: 'Font Awesome 5 Pro';
    display: inline-block;
    margin-left:.15rem;
    font-size: 55%;
    transform: translate(0%, -50%);
}

.footer {
    background-color:$dark;
    margin-top:auto;
    padding: 1rem 0rem;
    p {
      text-align:center;
      color:#fff;
      font-size:.8rem;
    }
    ul {
      text-align:center;
      li {
        color:#fff;
        font-size: .8rem;
        a {
          color:#fff;
          font-size: .8rem;
          &:hover {
            text-decoration:none;
            box-shadow: 0 0.15rem $teal;
            transition: box-shadow .2s, color .2s;
          }
        }
        &:not(:last-child):after {
          content: '';
          display: inline-block;
          position: relative;
          margin-left:.8rem;
          top:.2rem;
          width: 1px;
          height: 1rem;
          background: rgba(255, 255, 255, 0.6);     
        }
      }
    }
}

// ============================================= Bootstrap 4 Components ============================================= //

.card {
    .card-title {
        font-size:1.3rem;
        margin-bottom:0rem;
    }
}

.popover {
    font-size:$font-size-base;
    box-shadow:$box-shadow;
    max-width:30rem;
    margin:1rem; // for mobile sizes
}

.dropdown-menu {
    box-shadow:$box-shadow;
}

.list-group {
    &.list-group-flush {
        .list-group-item {
            padding: 0.3rem .2rem;
            border:none;
            &.lvl2 {
                padding-left:.9rem;
            }
            &.lvl3 {
                padding-left:1.8rem;
            }
            &.active {
                background:#fff;
                color:$primary;
                font-weight:bold;
            }
        }
    }
}

.list-inline {
    li {
        display:inline-block;
        &:not(:last-child) {
            margin-right:.5rem;
        }
    }
}  

.modal {
    .modal-dialog {
        .modal-content {
            .modal-header {
                font-size:1.1rem;
                font-family: $montserrat;
                font-weight:700!important;
            }
        }
    }
    &.modal-xl {
        max-width:calc(100% - 30px);
    }
}

.badge {
    vertical-align: 10%;
}

.alert {
    .alert-heading {
        font-size:1.1rem;
        font-family: $montserrat;
        font-weight:700!important;
        margin-bottom:.5rem;
        display:inline-block;       
    }
    p {
        margin-bottom:.5rem;
    }
    a:hover, a:focus {
        text-decoration:underline!important;
    }
}  

// ============================================= Custom I2E Components ============================================= //

ul.no-bullets {
    list-style: none;
}

// back to top button
#back-to-top {
    display: none;
    position: fixed;
    bottom: .7rem;
    right: .8rem;
    z-index: 1090;
    cursor: pointer;
    font-size: .7rem;
    padding: 0.6rem 0.6rem 0.5rem .6rem;
    border-radius: 0.8rem;
    line-height: 0.7rem;
    i {
        margin-bottom:.1rem;
    }
}

// apply class .collapse-toggle to link or button to give them plus and minus icons NOTE: must use bootstrap collapse functionality
.collapse-toggle {
    &:hover {
        text-decoration:none;
        &:before {
            color:$blue-800;
        }
        &:after {
            color:$blue-800;
        }
    }
    &:before, &[aria-expanded=false]:before {
        content: '\f055';
        font-family: 'Font Awesome 5 Pro';
        display: inline-block;
        font-weight:300;
        color:$blue-700;
    }
    &[aria-expanded=true]:before {
        content: '\f056';
    }
    &:after, &[aria-expanded=false]:after {
        content: 'Show';
        font-family: $noto-sans;
        display: inline-block;
        font-weight:regular;
        color:$blue-700;
        margin-left:.2rem;
    }
    &[aria-expanded=true]:after {
        content: 'Hide';
    }
}


.info-card-deck {
    .card {
        border-radius:0rem;
        border-color:transparent;
        border-right-color:$gray-300;
        padding-right: 1rem;
        &:last-child {
            border-right-color:transparent;
        }
        .info-label {
            font-family: $montserrat;
            line-height: 1.1;
            font-size:1rem;
            margin-bottom:.2rem;
        }
        label.info-label {
            font-weight:normal;
        }
    } 
}

.tag {
    display: inline-block;
    padding: .35em .5em;
    font-size: 90%;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    border:1px solid;
    &.tag-primary {
        background-color: $blue-100;
        color:$blue-900;
        border-color:$blue-300;
    }
    &.tag-secondary {
        background-color: $gray-100;
        color:$gray-900;
        border-color:$gray-300
    }
    &.tag-success {
        background-color: $green-100;
        color:$green-900;
        border-color:$green-300;
    }
    &.tag-danger {
        background-color: $red-100;
        color:$red-900;
        border-color:$red-300;
    }
    &.tag-warning {
        background-color: $yellow-100;
        color:$yellow-900;
        border-color:$yellow-300;
    }
    &.tag-info {
        background-color: $teal-100;
        color:$teal-900;
        border-color:$teal-300;
    }
}



// Dimmer / Spinner / Loading //
.dimmer {
  background:rgba(0,0,0,.6);
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  display:flex;
  justify-content: center;
  align-items:center;
  z-index:9999;
    @keyframes rotate { to {  transform: rotate(1turn) }}
    .loading {
        text-align: center;
    }
    .spinner {
        width: 50px;
        height: 50px;
        display: inline-block;
        border: 6px solid rgba(189,189,189,0.25);
        border-left-color: rgba(45,199,153,1);
        border-top-color: rgba(45,199,153,1);
        border-radius:50%;
        animation: rotate 800ms infinite linear;
    }
    .loadMsg {
        font-size: 1.3rem;
        color:#fff;
        font-weight: bold;
    }
}

#side-nav {
    position: sticky;
    top: 1rem;
    overflow-y: auto;
    margin-bottom:2rem;
    ul {
        li {
            // removes all of bootstraps styles
            padding:0rem;
            border:none;
            margin-bottom:0rem;
            background-color:transparent;
            a {
                display:block;
                padding: .75rem .5rem;
                color:$gray-900;
                border-radius:0rem;
                border-bottom:1px solid $gray-300;
                &.parent {
                    padding-right:1.75rem; //space for plus/minus icon
                    // minus icon 
                    &[aria-expanded=false]:after {
                        font-family: "Font Awesome 5 Pro";
                        font-weight: 400;
                        content:'\f055';
                        position: absolute;
                        right: .5rem;
                        top: .75rem;
                    }
                    // plus icon
                    &[aria-expanded=true]:after {
                        font-family: "Font Awesome 5 Pro";
                        font-weight: 400;
                        content:'\f056';
                        position: absolute;
                        right: .5rem;
                        top: .75rem;
                    }
                }
                &.active {
                    border-left: .3rem solid $teal;
                    padding-left: calc(1.5rem - .3rem);
                    font-weight:bold;
                }
            }
        }
        // nested ul
        ul {
            padding:0rem!important;
            li {
                a {
                    padding-left: 1.5rem;
                    &.active {
                        padding-left: calc(1.5rem - .3rem);
                    }
                }
            }
        }
    }
}

.infoTooltip {
    padding: .2rem;
    margin-left: -.2rem;
}
.infoTooltip > i {
    line-height: inherit;
    vertical-align: top;
    font-size: 90%;
}

.more-info {
    padding:0px 2px;padding: 0rem .2rem;
    margin-left: -.2rem;
    cursor: pointer;
    color:$primary;
}
h1, h2, h3 {
    .infoTooltip i {
        font-size: 85%;
    }
}

.hr-text {
    line-height: 1em;
    position: relative;
    outline: 0;
    border: 0;
    text-align: center;
    height: 1.5em;
    &:before {
        content: '';
        background: $gray-300;
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: 1px;
    }
    &:after {
        content: attr(data-content);
        position: relative;
        display: inline-block;
        padding: 0 .5em;
        line-height: 1.5em;
        color: $gray-900;
        background-color: #fcfcfa;
        text-transform: uppercase;
        font-weight:bold;
    }
  }