// -------------------------------------------------------------------------------- Program Recommended Costs Table

.funds-input {
    min-width: 7rem;
    display:inline-block;
    max-width:9rem;
}
@media (max-width: 1700px) { 
    .funds-input {
        max-width: 7rem;
    }
}

.percent-input {
    min-width: 3rem;
    max-width:3rem;
    display:inline-block;
}

.prc-table {
    border: 1px solid #c1c2c9;
    thead {
        tr {
            th {
                border-bottom: 1px solid #c1c2c9;
                text-align: right;
                &:first-of-type {
                    text-align:left;
                    border-right:1px solid #c1c2c9;
                }
            }
            &:last-of-type {
                border-bottom: 2px solid #c1c2c9;
            }
            &:first-of-type {
                .read-only-th {
                    text-align: center;
                }
            }
        }
    }
    tbody {
        tr {
            td {
                text-align:right;
                &:first-of-type {
                    border-right:1px solid #c1c2c9;
                    text-align:left;
                }
            }
        }
    }
}

.fsource-fy1-form {
    .form-row {
        flex-wrap:nowrap;
    }
    .left-label {
        width:6rem;
        max-width:6rem;
        text-align:right;
        label {
            justify-content: flex-end;
        }
    }
    .right-label {
        text-align:left;
        font-weight:bold;
    }
    .input-group {
        flex-wrap:nowrap;
    }
    input {
        text-align:right;
    }
}

.read-only-th {
    display: inline-block;
    width: auto;
    vertical-align: middle;
    min-width:7rem;
    padding:0rem 0.75rem;
}

// -------------------------------------------------------------------------------- Drag & Drop

.drop-well {
    width:100%;
    display:flex;
    padding:.5rem;
    border-radius:.5rem;
    border:1px solid $gray-400;
    box-shadow: 2px 2px 4px -2px rgba(0,0,0,0.2) inset;
    flex-direction: column;
}

.cdk-drop-list {
    &:hover {
        cursor:grab;
    }
    &.cdk-drop-list-dragging {
        cursor:move;
    }
}

.drag-item {
    border-radius: $border-radius;
    padding:.5rem 1rem;
    display:flex;
    align-items:center;
    border:1px solid $gray-400;
    width: 100%;
    margin-bottom:.5rem;
    background:white;
    &:before {
        content: '\f7a4';
        font-family: "Font Awesome 5 Pro";
        display:inline-block;
        color:$gray-600;
        font-size:1rem;
        font-weight:900;
        margin-right:.75rem;
    }
    &.drag-item-locked {
        background:$gray-100;
        &:before {
            content: '\f023';
        }
    }
    &.drag-item-placeholder {
        border:none;
        background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='4' ry='4' stroke='black' stroke-width='1' stroke-dasharray='6%2c 6' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
    }
}

.drag-item {
    .doc-name {
        font-weight:bold;
        margin-right:.5rem;
    }
    .doc-desc {
        font-style:italic;
    }
}


// -------------------------------------------------------------------------------- Progress Bar

.progressbar {
    counter-reset: step;
    li {
        list-style-type: none;
        width: 25%;
        float: left;
        font-size: .9rem;
        position: relative;
        text-align: center;
        text-transform: none;
        a {
            color: $gray-600;
            &:hover, &:focus {
                text-decoration: underline!important;
            }
        }
        &:before {
            width: 30px;
            height: 30px;
            content: counter(step);
            counter-increment: step;
            line-height: 30px;
            border: 2px solid $gray-400;
            display: block;
            text-align: center;
            margin: 0 auto 4px auto;
            border-radius: 50%;
            background-color: white;
            color:$gray-600;
            font-family: $montserrat;
            font-weight:700;
        }
        &:after {
            width: 100%;
            height: 2px;
            content: '';
            position: absolute;
            background-color: $gray-400;
            top: 15px;
            left: -50%;
            z-index: -1;
        }
        &:first-child:after {
            content: none;
        }
        &.active {
            text-align:auto;
            font-weight: bold;
            &:before {
                border-color: $gray-600;
                background-color:$gray-600;
                color:white;
            }
            & + li:after {
                color: $green-600;
            }
        }
        &.complete:before {
            border-color:$green-600;
            background-color:$green-600;
            color:white;
            content:'';
        }
    }
}


// data table

.dt-controls {
    display:flex;
    align-items: center;
    margin-bottom:.5rem;
    label {
        margin-bottom:0rem;
    }
    .dataTables_length {
        float:unset;
        display: inline-block;
        select {
            padding:2px;
        }
    }
    .dt-buttons {
        float:unset;
        display: inline-block;
        margin-right:1rem;
        .btn-excel {
            color: $gray-600;
            padding: $btn-padding-y-sm $btn-padding-x-sm;
            font-size: $font-size-sm;
            line-height:$line-height-sm;
            border-radius: $border-radius-sm;
            display: inline-block;
            background: transparent;
            border: 1px solid $gray-600;
            transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
            margin-right:0rem;
            margin-bottom:0rem;
            &:focus {
                box-shadow: 0 0 0 .2rem rgba(108,117,125,.5);
                background: transparent;
            }
            &:hover {
                background: rgba($secondary, .15);
                color:darken($secondary, 15%);
            }
            &:before {
                display:inline-block;
                font-family: 'Font Awesome 5 Pro';
                content: '\f1c3';
                margin-right: .2rem;
            }
        }
    }
    .dataTables_filter {
        float:unset;
        display: inline-block;
        margin-right:1rem;
        input {
            height: $input-height-sm;
            padding: $input-padding-y-sm $input-padding-x-sm;
            @include font-size($input-font-size-sm);
            line-height: $input-line-height-sm;
            @include border-radius($input-border-radius-sm);
        }
    }
    .dataTables_paginate {
        float:unset;
        display: flex;
        & > span {
            display:flex;
        }
        .paginate_button {
            position: relative;
            display: block;
            padding: $pagination-padding-y-sm $pagination-padding-x-sm;
            margin-left: -$pagination-border-width;
            line-height: $line-height-sm;
            color: $pagination-color;
            background: $pagination-bg;
            border: $pagination-border-width solid $pagination-border-color;
            font-size:$font-size-sm;
            border-radius:0rem;
            &:hover {
                z-index: 2;
                color: $pagination-hover-color!important;
                text-decoration: none;
                background: $pagination-hover-bg;
                border-color: $pagination-hover-border-color;
            }
            &:focus {
                z-index: 2;
                outline: $pagination-focus-outline;
                box-shadow: $pagination-focus-box-shadow;
            }
            &.current {
                z-index: 1;
                color: white!important;
                background: $pagination-active-bg;
                border-color: $pagination-active-border-color;
                &:hover {
                    color: white!important;
                    background: $pagination-active-bg;  
                }
            }
            &.disabled {
                color: $gray-400!important;
                pointer-events: none;
                cursor: auto;
                background: $pagination-disabled-bg;
                border-color: $pagination-disabled-border-color;
            }
        }
        & > .paginate_button {
            &:first-child {
                margin-left: 0;
                @include border-left-radius($border-radius-sm);
            }
            &:last-child {
                @include border-right-radius($border-radius-sm);
            }
        }
    }
}
table ~ .dt-controls {
    margin-top:.5rem;
}

.next-approvers-table {
    tbody {
        tr {
            &.cdk-drag {
                .order-icon:before {
                    content: '\f7a4';
                    font-family: "Font Awesome 5 Pro";
                    display:inline-block;
                    margin-right:.75rem;
                    color:$gray-600;
                    font-weight:900;
                }
                &.cdk-drag-disabled {
                    .order-icon:before {
                        content: '\f023';
                    }    
                }
            }
            td {
                vertical-align:middle;
            }
        }
    }
}

.workflow-comments {
    display:flex;
    flex-direction: row;
    .comment-content {
        margin-left:1rem;
        .comment-details {
            font-size:$font-size-sm;
            font-style:italic;
        }
    }
}