// bootstrap theme colors
$blue: $blue-700;
$purple: #606eb2;
$red: $red-700;
$orange: #FE5000;
$yellow: $yellow-400;
$green: $green-600;
$teal: $teal-500;
$dark: $blue-900;

// ------------------------------------------------------------------------ TYPE 

$font-size-base:              .9rem;
$font-family-sans-serif:      $noto-sans;
$line-height-base:            1.3;

$headings-font-family:        $montserrat;
$headings-font-weight:        700;
$headings-line-height:        1.05;
$headings-color:              $gray-900;

$h1-font-size:                $font-size-base * 1.8;
$h2-font-size:                $font-size-base * 1.6;
$h3-font-size:                $font-size-base * 1.4;
$h4-font-size:                $font-size-base * 1.3;
$h5-font-size:                $font-size-base * 1.2;
$h6-font-size:                $font-size-base;

// ------------------------------------------------------------------------ FORMS

$input-color:                           $gray-900;

// Grid containers
// Define the maximum width of `.container` for different screen sizes.
$container-max-widths: (
  sm: 1000px,
  md: 1000px,
  lg: 1280px,
  xl: 1800px
);